<template>
	<view class="container">
		<view class="listBox">
			<view class="list" v-for="(item,index) in myList" :key="index" @click="listTap(index)">
				<view>
					<text>{{item.name}}</text>
				</view>
				<uni-icons class="icon" type="arrowright" size="14" color="#999"></uni-icons>
			</view>
		</view>
	</view>
</template>

<script>
	import httpUtils from '@/utils/httpUtils.js';
	import Tools from '@/utils/tools.js';
	export default {
		data() {
			return {
				myList: [{
					name: '个人信息',
				}, {
					name: '修改密码',
				}]
			}
		},
		methods: {
			listTap(index) {
				switch (index) {
					case 0:
						uni.navigateTo({
							url: '/pages/salemanVisual/more/myInfo'
						})
						break
					case 1:
						uni.navigateTo({
							url: '/pages/salemanVisual/more/modifyPwd'
						})
						break
				}
			}
		}
	}
</script>

<style lang="scss">
	page {
		background-color: #f6f6f6;
	}

	.listBox {
		background-color: #fff;
		.list {
			padding: 30rpx;
			border-bottom: 1rpx solid #eee;
			display: flex;
			align-items: center;
			justify-content: space-between;
			font-size: 26rpx;

			view {
				display: flex;
				align-items: center;

				image {
					width: 40rpx;
					height: 40rpx;
					margin-right: 20rpx;
				}
			}
		}
	}
</style>
